import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { RoleManageBean } from './../bean/role-manage-bean';
import { CommonHttpService } from '../../../http/common.http.service';
import { ConfirmationService } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
import { checkName } from 'app/@uisftech/common/service/validator';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
    selector: 'allot-business-field',
    templateUrl: './allot-business-field.component.html',
    styleUrls: ['./allot-business-field.component.scss']
})
export class AllotBusinessFieldComponent implements OnInit {
    @Input() inValue: any;
    @Output() outValue: EventEmitter<any> = new EventEmitter();
    // 表体内容
    tableData: any;
    roleManageBean: RoleManageBean = new RoleManageBean()
    operTypeList: Array<any> = [];//操作类型列表
    //模糊搜索展示的列表
    sourceList: Array<any> = [];
    targetList: Array<any> = [];
    //提示信息
    msgs: Message[] = [];
    //未选数据列表
    sourceListData: Array<any> = [];
    //已选数据列表
    targetListData: Array<any> = [];
    //
    chooseList: Array<any> = [];
    //操作类型
    operType: string = '';
    //角色编号
    roleId: string;
    constructor(
        public commonHttpService: CommonHttpService,
        public confirmationService: ConfirmationService,
        public commfunc: Commfunc
    ) {
        this.operTypeList = [
            { type: '新增', id: 'add', index: '1' },
            { type: '修改', id: 'update', index: '2' },
            { type: '删除', id: 'del', index: '3' },
            { type: '查看', id: 'look', index: '4' },
        ]
    }

    ngOnInit() {
        this.roleId = this.inValue.roleId;
        
        let source = new Promise((resolve) => {
            //左边数据查询
            let param = {
                pageSize: '999999',
                pageNum: '1'
            }
            this.commonHttpService.querySpContent(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    // this.sourceListData = data.contentList;
                    resolve({ sourceList: data.contentList });
                }
            })
        })
        let target = new Promise((resolve) => {
            //右边数据查询
            let param1 = {
                roleTypeId: this.roleId,
                pageSize: '999999',
                pageNum: '1'
            }
            this.commonHttpService.querySpContentPurposeOperation(param1).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    if (data.contentList) {
                        this.targetListData = data.contentList;
                        this.targetList = data.contentList;
                        resolve({ targetList: data.contentList })
                    }
                }
            })
        })

        let all = Promise.all([ source,target]);
        all.then((res) => { //去除左边包含的右边的数据
            let sourceListData = res[0]['sourceList'];
            let targetListData = res[1]['targetList'];
            for (let i = 0; i < targetListData.length; i++) {
                for (let j = 0; j < sourceListData.length; j++) {
                    if (targetListData[i]['contentId'] == sourceListData[j]['contentId']) {
                        sourceListData.splice(j, 1);
                        break;
                    }
                }
            }
            this.sourceList = sourceListData;
            this.sourceListData=sourceListData;
        })
    }

    //模糊搜索
    search(event, flag) {
        let inp = event.srcElement.value;
        if (flag == 'source') {
            this.sourceList = this.sourceListData.filter(item => item['contentName'].includes(inp))
            if (inp == '') {
                this.sourceList = this.sourceListData;
            }
        } else {
            this.targetList = this.targetListData.filter(item => item['contentName'].includes(inp))
            if (inp == '') {
                this.targetList = this.targetListData;
            }
        }
    }

    //操作类型
    checked(index) {
        if (this.operType.indexOf(index) == -1) {
            this.operType += index + ",";
        } else {
            this.operType = this.operType.replace(index, '');
        }

    }

    //选中样式
    choose(event, item, flag) {
        if (this.operType.length == 0 && flag == 0) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '请先勾选操作类型' });
            return
        }
        let txt = event.srcElement.innerText;
        if (this.chooseList.indexOf(txt) == -1) {
            this.chooseList.push(txt);
            event.srcElement.style.backgroundColor = "rgba(198, 223, 239, 0.94)";
        } else {
            this.chooseList.splice(this.chooseList.indexOf(txt), 1);
            event.srcElement.style.backgroundColor = "transparent";
        }
    }

    //将数据移到另一边
    move(listData, listData1) {
        this.operType = this.operType.slice(0, this.operType.length - 1);
        let list = [];
        let list1 = [];
        for (let i = 0; i < this.chooseList.length; i++) {
            for (let j = 0; j < listData.length; j++) {
                if (this.chooseList[i] == listData[j].contentName) {
                    listData[j]['contentOperationId'] = this.operType;
                    listData1.push(listData[j]);
                    listData.splice(j, 1);
                    break
                }
            }
        }
        list1 = listData1;
        list = listData;
        this.chooseList = [];
        return { list, list1 }
    }

    //右移
    moveRight() {
        let data = this.move(this.sourceListData, this.targetListData);
        this.sourceList = data.list;
        this.targetList = data.list1;
    }

    //全部右移
    moveAllRight() {
        this.operType = this.operType.slice(0, this.operType.length - 1);
        this.sourceListData.forEach(item => {
            item['contentOperationId'] = this.operType;
        })
        this.targetListData = this.targetListData.concat(this.sourceListData);
        this.targetList = this.targetListData;
        this.sourceList = [];
        this.sourceListData = [];
    }

    //左移
    moveleft() {
        let data = this.move(this.targetListData, this.sourceListData);
        this.sourceList = data.list1;
        this.targetList = data.list;
    }

    //全部左移
    moveAllLeft() {
        this.sourceListData = this.sourceListData.concat(this.targetListData);
        this.sourceList = this.sourceListData;
        this.targetList = [];
        this.targetListData = [];
    }

    //确定按钮
    confirmAllot() {

        let contentSpList = [];
        for (let j = 0; j < this.targetListData.length; j++) {
            let contenMap = {};
            contenMap["contentOperationId"] = this.targetListData[j]["contentOperationId"];
            contenMap["contentName"] = this.targetListData[j]["contentName"];
            contenMap["contentPurposeTypeId"] = this.targetListData[j]["contentId"];
            contenMap["roleId"] = this.roleId;
            contentSpList.push(contenMap);
        }
        let param = {
            relList: contentSpList,
            roleTypeId: this.roleId
        }
        this.commonHttpService.createSpContentPurposeOperation(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.outValue.emit({ display: false, message: data.returnCode.message });
                this.targetListData = [];
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }


    //取消按钮
    cancelAllot() {
        this.outValue.emit({ display: false });
        // this.selectFeild = [];
    }


}
