import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { RoleManageBean } from './../bean/role-manage-bean';
import { CommonHttpService } from '../../../http/common.http.service';
import { Message } from 'primeng/primeng';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { checkName, checkRoleNum } from 'app/@uisftech/common/service/validator';

@Component({
    selector: 'role-modeify-manage',
    templateUrl: './role-modify-manage.html',
    styleUrls:['role-modeify-manage.scss']
})

export class RoleModeifyManage implements OnInit {
    @Input() inValue: any;

    @Output() outValue: EventEmitter<any> = new EventEmitter();
    //提示信息
    msgs: Message[] = [];
    userform: FormGroup; //校验        

    //实体
    roleManageBean: RoleManageBean = new RoleManageBean();
    constructor(
        public commonHttpService: CommonHttpService,
        public fb: FormBuilder,
    ) { }

    ngOnInit() {
        this.userform = this.fb.group({
            'roleName': new FormControl('', [Validators.pattern(/^[\u4E00-\u9FA5]{1,20}$/), Validators.required]),
            // 'roleId': new FormControl('', [Validators.pattern(/^[\w]{6,20}$/),Validators.required]),
            'roleDescribe': new FormControl('', [Validators.pattern(/^[\u4e00-\u9fa5a-zA-Z0-9\u3002\uff01\uff0c\u3001\uff1b\uff1a\u201c\u201d\u2018\u2019\uff08\uff09\u300a\u300b\u300b\u3008\u3009\u3010\u3011\u300e\u300f\u300c\u300d\ufe43\ufe44\u3014]{1,60}$/), Validators.required])
        });
    }

    ngOnChanges() {
        this.fuzhi()

    }
    fuzhi() {
        this.roleManageBean = this.inValue;
    }
    //提交
    preserveClick() {
        //校验的
        for (const i in this.userform.controls) {
            this.userform.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.userform.controls) {
            if (!this.userform.controls[i].valid) {
                return;
            }
        }
        // 
        this.commonHttpService.modifyRole(this.roleManageBean).subscribe(data => {
            if (data.returnCode.code == "success") {
               
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                this.outValue.emit(false);
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        });

    }
    //取消
    modifyCloseBtn() {
        this.outValue.emit(false);
        this.roleManageBean = new RoleManageBean();
    }
    //选择树的结果
    treeClick(param) {

    }

    //取消
    reset() {
        this.outValue.emit({ display: false });
    }

}