import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { RoleManageBean } from './../bean/role-manage-bean';
import { RadioButtonModule } from 'primeng/primeng'
import { CommonHttpService } from '../../../http/common.http.service';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { Message } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
    selector: 'role-copy-manage',
    templateUrl: 'role-copy-manage.html',
    styleUrls:['role-copy-manage.scss']
})

export class RoleCopyManage implements OnInit {
    @Input() inValue: any;

    @Output() outValue: EventEmitter<any> = new EventEmitter();
    //数据实体
    roleManagerBean: RoleManageBean = new RoleManageBean();
    roleStatus: any = [];
    //提示信息
    msgs: Message[] = [];
    roleId: string;
    roleName: string;
    isCopyPower: string='N';//复制的权限
    userform: FormGroup; //校验  
    constructor(
        public commonHttpService: CommonHttpService,
        public fb: FormBuilder,
        public commfunc: Commfunc
    ) { }

    ngOnInit() {
        //接受父页面传过来的值
        this.roleId = this.inValue.roleId;
        this.roleName = this.inValue.roleName;
        this.userform = this.fb.group({
            'roleId': new FormControl('', [Validators.pattern(/^[\w]{1,20}$/), Validators.required]),
            'roleName': new FormControl('', [Validators.pattern(/^[\u4E00-\u9FA5]{1,20}$/), Validators.required]),
            'roleDescribe': new FormControl('', [Validators.pattern(/^[\u4e00-\u9fa5a-zA-Z0-9\u3002\uff01\uff0c\u3001\uff1b\uff1a\u201c\u201d\u2018\u2019\uff08\uff09\u300a\u300b\u300b\u3008\u3009\u3010\u3011\u300e\u300f\u300c\u300d\ufe43\ufe44\u3014]{1,60}$/), Validators.required])
        });
        this.fuzhi();
    }
    //勾选复制权限事件
    onChanges(event) {
        if (event) {
            this.isCopyPower = 'Y';
        } else {
            this.isCopyPower = 'N';
        }
    }

    fuzhi() {
        this.roleManagerBean = this.inValue;
    }
    copyClick() {
        //校验的
        for (const i in this.userform.controls) {
            this.userform.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.userform.controls) {
            if (!this.userform.controls[i].valid) {
                return;
            }
        }
        let param = {
            copyRoleId: this.roleId,
            roleId: this.roleManagerBean.roleId,
            roleName: this.roleManagerBean.roleName,
            roleDescribe: this.roleManagerBean.roleDescribe,
            isCopyFlag: this.isCopyPower
        }
        this.commonHttpService.copyRole(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.outValue.emit(false);
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }
    closeClick() {
        this.outValue.emit(false);
        this.roleManagerBean = new RoleManageBean();
    }
}