import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { RoleManageBean } from './bean/role-manage-bean';
import { CommonHttpService } from '../../http/common.http.service';
import { ConfirmationService } from 'primeng/primeng';
import { Message, SelectItem } from 'primeng/primeng';
import { checkName } from 'app/@uisftech/common/service/validator';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { DomSanitizer } from '@angular/platform-browser';
import { checkCard, checkTel } from 'app/@uisftech/common/service/validator';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Validators, FormControl, AbstractControl, FormGroup, FormBuilder } from '@angular/forms';

@Component({
    selector: 'role-manage',
    styleUrls: ['role-manage.scss'],
    templateUrl: 'role-manage.html',
    providers: [ConfirmationService, Commfunc]
})

export class RoleManage implements OnInit {
    precredit: string = '角色查询';
    // 表格中显示的信息
    tabMesg: any = tableMessage;
    // 表体内容
    tableData: any;
    roleManageBean: RoleManageBean = new RoleManageBean()
    //角色状态
    roleStatus: any = [];

    //总页数
    total: string;
    //每页多少条
    pageSize: number = 10;

    //提示信息
    msgs: Message[] = [];

    //模态框的标题
    headerTitle: string = "修改";
    //模态框是否显示
    display: boolean = false;
    showModel: string;

    //传出的值
    inValue: string;

    //修改的参数
    updateValue: any = [];
    //复制传参
    copyValue: any = [];
    power: any;
    first: any = 0;
    //测试校验
    myForm: FormGroup;
    submitted: boolean;
    genders: SelectItem[];
    description: string;
    rows: any;
    currentPage: any;
    businessFieldValue: any;
    constructor(
        public commonHttpService: CommonHttpService,
        public confirmationService: ConfirmationService,
        public fb: FormBuilder,
        public router: Router,
        public commfunc: Commfunc,
        //  ab: AbstractControl,
        public san: DomSanitizer
    ) {
        this.createForm()
    }

    ngOnInit() {
        this.roleManageBean.pageSize = 10;
        this.roleManageBean.pageNum = 1;
        this.queryClick();
        this.queryPower()
        //按回车键查询
        let _thiss = this;
        document.onkeydown = function (event) {
            if (event && event.keyCode === 13) {
                _thiss.clickQuery();
            }
        }
    }

    createForm() {
        this.myForm = this.fb.group({
            mobile: ['', [Validators.required, checkTel]]
        })
    };
    postDate() {
        if (this.myForm.valid) {
        }
    }

    //查询
    queryClick() {
        // if (this.roleManageBean.roleId.length > 0 || this.roleManageBean.roleName.length > 0) {
        //     this.roleManageBean.pageNum = 1;
        // } else {
        //     this.roleManageBean.pageNum = this.currentPage;
        // }
        this.commonHttpService.queryRoleListInfo(this.roleManageBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableData = data.roleListInfo;
                this.total = data.total;
                // this.msgs = [];
                // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }
    clickQuery() {
        this.roleManageBean.pageNum = 1;
        this.first = 0;
        this.commonHttpService.queryRoleListInfo(this.roleManageBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableData = data.roleListInfo;
                this.total = data.total;
                // this.msgs = [];
                // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }
    queryPower() {
        let param = {
            pageSize: '100000',
            pageNum: '1'
        }
        this.commonHttpService.querySecurityPermissionList(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.power = data.permissionList;
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }

    //重置
    resetO() {
        this.roleManageBean.roleId = '';
        this.roleManageBean.roleName = '';
    }
    //列表查询
    query() {
        this.commonHttpService.queryRoleInfo(this.roleManageBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableData = data.roleListInfo;
                this.total = data.total;
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }

    //分页
    paginate(event) {
        //每页显示的条数
        this.rows = event.rows;
        this.roleManageBean.pageSize = this.rows;
        //当前页
        this.currentPage = event.page + 1;
        this.roleManageBean.pageNum = this.currentPage;
        this.first = event.page * this.roleManageBean.pageSize;
        //调用查询的方法
        this.queryClick();
    }


    //修改
    update(item) {
        this.headerTitle = '角色修改';
        this.display = true;
        this.showModel = '2';
        this.updateValue = item;
    }

    //修改的回调
    updateCall(param) {

        this.display = param;
        // this.roleManageBean = new RoleManageBean();
        // this.roleManageBean.pageSize = 10;

        // this.roleManageBean.pageNum = 1;
        this.queryClick();
    }


    // 复制
    copy(item) {
        this.headerTitle = '角色复制';
        // this.inValue = car.vin;
        this.display = true;
        this.showModel = '3';
        this.copyValue = item;
    }
    copyCall(param) {
        this.display = param;
        // this.roleManageBean.pageNum = 1;
        this.queryClick();
    }
    // 分配权限
    allot(item) {
        this.headerTitle = '分配权限';
        this.display = true;
        this.showModel = '4';
        this.updateValue = { roleId: item.roleId, roleName: item.roleName };
    }
    //分配权限的回调
    allotCall(param) {
        this.display = param;
        // this.roleManageBean.pageNum = 1;
        this.queryClick();
    }
    allotField(item) {
        this.headerTitle = '分配业务字段';
        this.display = true;
        this.showModel = '5';
        this.businessFieldValue = item;
    }
    allotFieldCall(param) {
        this.display = param.display;
        if (param.message) {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: '分配业务字段成功' })
            this.queryClick();
        }
    }
    //删除按钮
    delete(item) {
        this.confirmationService.confirm({
            message: '确认要删除吗?',
            accept: () => {
                // 条件

                let param = { roleId: item.roleId };
                this.commonHttpService.deleteRole(param).subscribe(data => {
                    if (data.returnCode.code == "success") {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: '删除成功' });
                        this.queryClick();
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: "删除失败" });
                })
            }
        });
    }

    //添加
    add() {
        this.headerTitle = '角色添加';
        this.display = true;
        this.showModel = '1';
    }

    //添加后返回的值
    addCall(param) {
        this.display = param;
        // this.roleManageBean.pageNum = 1;
        this.queryClick();
    }

    // 重置
    reset() {
        this.roleManageBean.roleId = "";
        // this.roleManageBean.postName = "";
    }


    //返回的值
    outValue(param) {
        this.display = param.display;
    }
    cc() {
        this.router.navigate(['/pages/tzb/uisftech-view/check']);
    }
    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id)
    }

}


