import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { RoleManageBean } from './../bean/role-manage-bean';
import { CommonHttpService } from '../../../http/common.http.service';

@Component({
    selector: 'role-details-manage',
    templateUrl: './role-details-manage.html',
})

export class RoleDetailsManage implements OnInit {
    @Input() inValue: string;

    @Output() outValue: EventEmitter<any> = new EventEmitter();
    //数据实体
    roleManageBean: RoleManageBean = new RoleManageBean();
    roleStatus: any = [];
    constructor(public httpService: CommonHttpService) {

    }

    ngOnInit() {
        //角色状态
        this.roleStatus.push({ label: 'Select City', value: null });
        this.roleStatus.push({ label: 'New York', value: { id: 1, name: 'New York', code: 'NY' } });
        this.roleStatus.push({ label: 'Rome', value: { id: 2, name: 'Rome', code: 'RM' } });
        this.roleStatus.push({ label: 'London', value: { id: 3, name: 'London', code: 'LDN' } });
    }

    //选择树的结果
    treeClick(param) {

    }

    //添加的结果
    add() {
        this.outValue.emit({ display: false });
    }

    //取消
    reset() {
        this.outValue.emit({ display: false });
    }

}