import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { RoleManageBean } from './../bean/role-manage-bean';
import { Message } from 'primeng/primeng';
import { CommonHttpService } from '../../../http/common.http.service';
import { checkName, checkRoleNum } from 'app/@uisftech/common/service/validator';
@Component({
  selector: 'role-add-manage',
  templateUrl: 'role-add-manage.html',
  styleUrls:['role-add-manage.scss'],
  providers: [CommonHttpService]
})

export class RoleAddManage implements OnInit {
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  userform: FormGroup; //校验        
  //提示信息
  msgs: Message[] = [];
  //  add_title: string = '产品线管理新增';   
  roleManageBean: RoleManageBean = new RoleManageBean();
  maxlen = 255;//默认最长长度
  remlen;
  constructor(
    public commonHttpService: CommonHttpService,
    public fb: FormBuilder,
  ) {
  }

  ngOnInit() {
    this.userform = this.fb.group({
      'roleId': new FormControl('', [Validators.pattern(/^[\w]{1,20}$/), Validators.required]),
      'roleName': new FormControl('', [Validators.pattern(/^[\u4E00-\u9FA5]{1,20}$/), Validators.required]),
      'roleDescribe': new FormControl('', [Validators.pattern(/^[\u4e00-\u9fa5a-zA-Z0-9\u3002\uff01\uff0c\u3001\uff1b\uff1a\u201c\u201d\u2018\u2019\uff08\uff09\u300a\u300b\u300b\u3008\u3009\u3010\u3011\u300e\u300f\u300c\u300d\ufe43\ufe44\u3014]{1,60}$/), Validators.required])
    });
  }

  // 保存
  preserveClick() {
    //校验的
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    }
    //提交服务
    // ;

    this.commonHttpService.createRole(this.roleManageBean).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.roleManageBean = new RoleManageBean();
        this.outValue.emit(false);
        this.roleManageBean.pageNum = 1;
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败" });
    })
  }
  //取消
  modifyCloseBtn() {
    this.outValue.emit(false);
    this.roleManageBean = new RoleManageBean();
  }
  //可输字数提示
  checkLen() {
  }
}