import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { RoleManageBean } from './../bean/role-manage-bean';
import { CommonHttpService } from '../../../http/common.http.service';
import { ConfirmationService } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
import { checkName } from 'app/@uisftech/common/service/validator';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
    selector: 'allot-power',
    styleUrls: ['allot-power.scss'],
    templateUrl: 'allot-power.html',
    providers: [ConfirmationService]
})

export class AllotPower implements OnInit {
    @Input() inValue: any;
    @Output() outValue: EventEmitter<any> = new EventEmitter();
    // 表体内容
    tableData: any;
    roleManageBean: RoleManageBean = new RoleManageBean()
    //角色状态
    roleStatus: any = [];
    //总页数
    total: string;
    //每页多少条
    pageSize: number = 10;
    //提示信息
    msgs: Message[] = [];
    //接受父页面传过来的值
    roleId: string = '';//角色编号
    roleName: string = '';//角色名称
    //接受所有分配权限的值
    power: any = [];
    //右边接受的参数
    selectPower: any = [];


    constructor(
        public commonHttpService: CommonHttpService,
        public confirmationService: ConfirmationService,
        public commfunc: Commfunc
    ) {

    }

    ngOnInit() {
        let param = {
            pageSize: '999999',
            pageNum: '1'
        }
        this.commonHttpService.querySecurityPermissionList(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.power = this.commfunc.listToLabelvalue(data.permissionList, "permissionsDescription", "permissionsId");
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })

    }

    ngOnChanges() {
        //接受父页面传过来的值
        this.roleId = this.inValue.roleId;
        this.roleName = this.inValue.roleName;

        //根据角色编号查询对应的角色权限
        let param = { 
            roleId: this.roleId ,
            pageSize: '999999'
        }
        this.commonHttpService.queryPermissionsListByRoleId(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.permissionsList) {
                    this.selectPower = this.commfunc.listToLabelvalue(data.permissionsList, 'description', 'permissionId');
                }
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })

        //删除左边数据里包含的右边数据
        setTimeout(() => {
            if (this.power) {
                this.selectPower.forEach((item) => {
                    this.power.forEach((items, i) => {
                        if (items.value == item.value) {
                            this.power.splice(i, 1);
                        }
                    })
                })
            }
        }, 500);
    }

    // 转换成list
    // tranList(value) {
    //     let roleList = [];
    //     if (value) {
    //         value.forEach(item => {
    //             roleList.push(item.value)
    //         });
    //     }
    //     return roleList;
    // }
    //确定按钮
    confirmAllot() {
        if(this.selectPower.length!=0){
            this.selectPower.forEach(item => {
                item.permissionsId = item.value;
                item.roleId = this.roleId;
                delete item.label;
                delete item.value;
            })
        }else{
            this.selectPower.push({roleId:this.roleId})
        }
        
        let param = { permissionsList: this.selectPower };
        // let param={permissionsList:this.tranList(this.selectPower),roleId:this.roleId}
        this.commonHttpService.maintainRolePermissions(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.outValue.emit(false);
                this.selectPower = [];
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }

    //取消按钮
    cancelAllot() {
        this.outValue.emit(false);
        this.selectPower = [];
    }
}